import React, { useEffect, useState } from "react"
import styled from "styled-components"
import { PrimaryCard } from "../../components/Card"
import Countdown from "react-countdown";
import { ButtonPrimary } from "../../components/Button";
import { useWeb3React } from "@web3-react/core";
import { MarketGenerationService } from "../../services/MarketGenerationService";
import ContentLoader from "react-content-loader";
import { CountdownRenderer } from "../../components/CountdownRenderer";
import { Claim } from "../Claim";
import { Contribute } from "../../components/Contribute";
import { generationEndDate } from "../../constants";

const PageWrapper = styled.div`
    padding-top:5em;

 ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-top:0em;
 `};
`

const CardContent = styled.div`
    display: grid;
    grid-gap: 1.5em;
    padding-bottom: 0.5em;
    width: 26em;


 ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    width: 100%;
    font-size: 0.75em;   
 `};
`

const Title = styled.div`
    font-family: Arial;
    padding: 0.5em 0;
    font-size: 2em;
    letter-spacing:0.025em;
    text-align: center
`

const Subtitle = styled.div`
    padding: 0.25em;
    font-size: 0.875rem;
    text-align: center
`

const Contribution = styled.div`
    font-size: 1.5em;    
    text-align: center
`

const ContributionInfo = styled.div`
    display: grid;
    grid-gap: 1em;
    padding: 0.5em 0;
`

const TextWrapper = styled.div`   
    justify-content: center;
    justify-self:center;
    dispay:grid;
    grid-template-columns: auto auto;
    color: ${({ theme }) => theme.text3};
`

const ConnectWalletWrapper = styled.div`
    padding-top: 2em;
`

const ContributionLoader = () => (
    <ContentLoader
        width={145}
        height={28}
        speed={1}
        animate={true}
        backgroundColor="#f6f6ef"
        foregroundColor="#e8e8e3"
        backgroundOpacity={0.06}
        foregroundOpacity={0.12}
    >
        <rect x="0" y="0" width="145" height="28" />
    </ContentLoader>)

export const MarketGenerationEvent = () => {
    const { account, library, chainId } = useWeb3React()
    const [contribution, setContribution] = useState<string>("")
    const [contributionLoading, setContributionLoading] = useState<boolean>(false)
    const [totalContribution, setTotalContribution] = useState<string>("")
    const [totalContributionLoading, setTotalContributionLoading] = useState<boolean>(false)
    const [active, setActive] = useState<boolean>(false)
    const [currentDateTime, setCurrentDateTime] = useState<Date>(new Date())

    useEffect(() => {
        const timer = setInterval(() => setCurrentDateTime(new Date()), 1000)
        return () => clearInterval(timer)
    },)

    const getContributionStatus = async () => {
        if (library && account && chainId) {
            const service = new MarketGenerationService(library, account!)
            setActive(await service.isActive())
            setContributionLoading(true)
            setContribution(await service.getContribution(account))
            setContributionLoading(false)

            setTotalContributionLoading(true)
            setTotalContribution(await service.getTotalContribution())
            setTotalContributionLoading(false)
        }
    }

    useEffect(() => {
        getContributionStatus()
        const timer = setInterval(() => getContributionStatus(), 30000)
        return () => clearInterval(timer)
    }, [library, account, chainId])

   
    return (
        <PageWrapper>
            <PrimaryCard width="auto">
                <CardContent>
                    <Title>Market Generation Event</Title>
                    <Countdown date={generationEndDate} renderer={CountdownRenderer} />
                    {account
                        ?
                        <>
                            <ContributionInfo>
                            <TextWrapper>
                                {totalContributionLoading ? <ContributionLoader /> : <Contribution>{totalContribution} USDT</Contribution>}
                                <Subtitle>Total contribution</Subtitle>
                            </TextWrapper>
                            <TextWrapper>
                                {contributionLoading ? <ContributionLoader /> : <Contribution>{contribution} USDT</Contribution>}
                                <Subtitle>My contribution</Subtitle>
                            </TextWrapper>
                            </ContributionInfo>
                            
                            {currentDateTime > generationEndDate && !active
                                ? <Claim />
                                : <Contribute onContribute={getContributionStatus}/> 
                            }
                        </>
                        :
                        <ConnectWalletWrapper>
                            <ButtonPrimary>Connect to a wallet</ButtonPrimary>
                        </ConnectWalletWrapper>}
                </CardContent>
            </PrimaryCard>
        </PageWrapper>
    )
}